      isOptionalChecked: (chainId) => {
        return optionChains.has(chainId)
      },
      handleChange: (chainId) => {
        if (chains.has(chainId)) {
          chains.delete(chainId)
        } else {
          chains.add(chainId)
        }
      },
      handleOptionChange: (chainId) => {
        if (optionChains.has(chainId)) {
          optionChains.delete(chainId)
        } else {
          optionChains.add(chainId)
        }
      },
    }
